import React from 'react';
import { withRouter } from "react-router-dom";
import { Button, Col, Form, Row } from 'antd';
import BaseComponent from '../BaseComponent';
import { FormButton, FormText } from '../../components/forms';

import { connect } from 'react-redux';

const mapStateToProps = state => ({
    user: state.identityReducer.user,
    admin: state.identityReducer.admin
})

var throttName = false;
var throttEmail = false;

class SignUp extends BaseComponent {
    constructor(props) {
        super(props)
        this.state = {
            statusName: "success",
            statusEmail: "success",
        }
    }

    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (values.username === '') {
                this.pushNotification("warning", "用户名是空的！", this.props.dispatch);
                return;
            }
            if (values.password === '') {
                this.pushNotification("warning", "密码是空的!", this.props.dispatch);
                return;
            }
            if (values.email === '') {
                this.pushNotification("warning", "邮箱是空的!", this.props.dispatch);
                return;
            }
            if (values.passwordAgain !== values.password) {
                this.pushNotification("warning", "两次输入的密码不一致!", this.props.dispatch);
                return;
            }

            let form = new FormData();
            form.append('username', values.username);
            form.append('password', values.password);
            form.append('email', values.email);

            var successAction = (result) => {
                this.props.switch()
                this.pushNotification("success", "注册成功！现在可以登录啦！");
                // this.props.history.push('/person')
                // this.pushNotification("warning", "请尽快填写个人信息，这有助于您忘记密码时修改密码");
            }

            var unsuccessAction = (result) => {
                this.pushNotification("warning", result.message);
            }

            this.post("/api/register", form, successAction, unsuccessAction)
        });
    }

    renderLogo = () => {
        return (
            <Row type="flex" justify='center'>
                <img alt="" style={styles.logo} src={require("@/assets/logo2.png")} />
            </Row>
        )
    }

    validUsername(rule, value, callback) {
        this.timeout(200).then(() => {
            if (throttName) return
            throttName = true

            let name = value
            let form = new FormData();
            form.append('name', name);

            this.timeout(100).then(() => {
                let successAction = (result) => {
                    let exist = result.data
                    if (exist) callback("用户名已存在");
                    else callback();
                }
                this.post('/api/existname', form, successAction, null);
                throttName = false
            })
        })
    }

    validEmail(rule, value, callback) {
        this.timeout(200).then(() => {
            if (throttEmail) return
            throttEmail = true

            let email = value
            let form = new FormData();
            form.append('email', email);

            this.timeout(100).then(() => {
                let successAction = (result) => {
                    let exist = result.data
                    if (exist) callback("邮箱已存在");
                    else callback();
                }
                this.post('/api/existemail', form, successAction, null);
                throttEmail = false
            })
        })
    }

    renderContent = () => {
        return (
            <Row type='flex'
                justify='center'
                align="middle"
                style={{ borderRadius: '20px' }}>
                <Col>
                    <Row
                        style={styles.cardContainer}>
                        <div style={styles.welcome}>欢迎来到CodePass</div>
                        <div style={styles.welcome2}></div>
                        <Form onSubmit={this.handleSubmit} type='flex' justify='center'>
                            <Row style={{ width: "100%" }} type="flex" justify='center'>
                                <Row style={{ width: "100%", marginLeft: "80px", color: '#AAAAAA', fontSize: 15 }}
                                    type="flex" justify='start'>请输入你的用户名:</Row>
                                <FormText form={this.props.form} validator={this.validUsername.bind(this)}
                                    name='username' required={true} icon="user" name1='用户名' />
                                <Row style={{ width: "100%", marginLeft: "80px", color: '#AAAAAA', fontSize: 15 }}
                                    type="flex" justify='start'>请输入你的邮箱:</Row>
                                <FormText form={this.props.form} validator={this.validEmail.bind(this)}
                                    name='email' required={true} icon="mail" name1='邮箱' />
                                <Row style={{ width: "100%", marginLeft: "80px", color: '#AAAAAA', fontSize: 15 }}
                                    type="flex" justify='start'>请输入你的密码:</Row>
                                <FormText form={this.props.form} style={{ marginBottom: "10px" }}
                                    name='password' required={true} icon="lock"
                                    inputType="password" name1='密码' />
                                <Row style={{ width: "100%", marginLeft: "80px", color: '#AAAAAA', fontSize: 15 }}
                                    type="flex" justify='start'>重复你的密码:</Row>
                                <FormText form={this.props.form}
                                    name="passwordAgain" required={true} icon='lock'
                                    inputType='password' name1='密码进行二次确认' />
                            </Row>
                            <Row type='flex' justify='center'>
                                <Col>
                                    <FormButton form={this.props.form} label="注册" style={styles.button} />
                                    <Button style={styles.button2} onClick={this.props.onCancel}>
                                        取消
                                    </Button>
                                </Col>
                            </Row>
                        </Form>
                        <Row type='flex' justify='center'>
                            <Col>
                                已经有账号了?<Button onClick={this.props.switch} type="link">现在登录吧！</Button>
                            </Col>
                        </Row>
                    </Row>
                </Col>
            </Row>
        );
    }

    render() {
        return (
            <Row>
                {this.renderLogo()}
                {this.renderContent()}
            </Row>
        );
    }

}


const styles = {

    logo: {
        height: '64px',
        width: '192px'
    },

    cardContainer: {
        width: '500px',
        marginTop: '10px'
    },

    button: {
        width: '350px',
        height: '40px',
    },

    button2: {
        width: '350px',
        height: '40px',
        color: 'white',
        backgroundColor: '#CCCCCC',
        marginBottom: '20px',
    },

    welcome: {
        fontSize: 25,
        marginLeft: '80px',
        marginRight: '10px',
        marginBottom: '3px',
    },
    welcome2: {
        fontSize: 17,
        color: '#AAAAAA',
        marginLeft: '80px',
        marginRight: '10px',
        marginBottom: '10px',
    },

};

export default Form.create()(connect(mapStateToProps)(withRouter(SignUp)))

